import { Product } from "./product"

export class Carro {
   public productos:  Product[] = new Array<Product>()
   public cantidad: string [] = new Array<string>()
   public total: string
   public iva: string
   public subtotal: string

   constructor(productos: Product[], cantidad: string[],total: string, iva: string, subtotal: string) {
    this.productos = productos;
    this.cantidad = cantidad
    this.total = total
    this.iva = iva
    this.subtotal = subtotal
   }
}