import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Carro } from '../models/carro';
import { ProductService } from '../services/product.service';

@Component({
  selector: 'app-carrito',
  templateUrl: './carrito.component.html',
  styleUrls: ['./carrito.component.css']
})
export class CarritoComponent implements OnInit {

  products: any = [ ];
  cantidades: any = [ ];
  subtotal: any 
  iva : any
  total: any

  isEmpty : boolean = false;


  constructor(
    private product_service : ProductService,
    private router: Router
  ) { }

  ngOnInit() {
    this.products = this.product_service.arrayProducts;
    this.cantidades = this.product_service.arrayCantidades;
    this.checkCart();
    this.getSubtotal();
  }

  removeToCar(){
    this.product_service.removeToCar();
    this.router.navigate(['/home']);
  }

  createSale(){
    var carrito = new Carro(this.products, this.cantidades, this.total, this.iva, this.subtotal)
    this.product_service.createFact(carrito).subscribe(
      result => {
        if (result.status == 200) {
          alert('Venta registrada')
        } else {
          alert('error')
        }
        this.router.navigate(['/home'])
      }
    );
  }

  getSubtotal(){
    console.log("Entra")
    var temp = 0;
    for (let i = 0; i < this.products.length; i++){
      temp += eval(this.products[i].price +'*'+this.cantidades[i]);
    }
    this.subtotal = temp;
    this.getIva(temp);
  }

  getIva(cantidad){
    this.iva = cantidad *.2
    this.getTotal()
  }

  getTotal(){
    this.total = this.iva + this.subtotal
  }

  checkCart(){
    if(this.products.length == 0 )
      this.isEmpty = true;
  }

  toCart(){
    
  }

}
