import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AboutusComponent } from './aboutus/aboutus.component';
import { AccessoriesComponent } from './accessories/accessories.component';
import { AdminComponent } from './admin/admin.component';
import { CarritoComponent } from './carrito/carrito.component';
import { DesktopComponent } from './desktop/desktop.component';
import { Details_productsComponent } from './details_products/details_products.component';
import { Display_detailsComponent } from './display_details/display_details.component';
import { GamerComponent } from './gamer/gamer.component';
import {HomeComponent} from './home/home.component';
import { LoginComponent } from './login/login.component';
import { ProfileComponent } from './profile/profile.component';
import { SingupComponent } from './singup/singup.component';

const routes: Routes = [
  
  {path: 'home', component: HomeComponent},
  {path: 'profile', component: ProfileComponent},
  {path: 'signup',component: SingupComponent},
  {path: 'login', component: LoginComponent},
  {path: 'accessories', component: AccessoriesComponent},
  {path: 'desktop', component: DesktopComponent},
  {path: 'gamer', component: GamerComponent},
  {path: 'aboutUs', component: AboutusComponent},
  {path: 'admin', component: AdminComponent},
  {path: 'details', component: Display_detailsComponent},
  {path: 'carrito', component: CarritoComponent},
  {path: 'detailsProd', component: Details_productsComponent},
  {path: '', redirectTo:'/home', pathMatch :'full'},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
