import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Carro } from '../models/carro';
import { Product } from '../models/product';

@Injectable({
  providedIn: 'root'
})
export class ProductService {

  private URL = 'http://localhost:3000/api'

  product_details: Product
  cart_products: Carro 
  arrayProducts: any = [ ];
  arrayCantidades: any = [ ];

  category_options = [
    'Gamer',
    'Accessory',
    'Desktop'

  ]

  constructor(
    private http: HttpClient
  ) { }

  getAllProduct() {
    return this.http.get<any>(this.URL + '/getAllProducts');
  }

  createProduct(product) {
    return this.http.post<any>(this.URL + '/createProduct', product);
  }

  setProductDetails(product) {
    this.product_details = product;
  }

  getProductDetails() {
    return this.product_details;
  }

  deleteProductByNameDB(product) {
    return this.http.post<any>(this.URL + '/deleteProduct', product);
  }

  updateProductByNameDB(product) {
    return this.http.post<any>(this.URL + '/updateProduct', product);
  }

  pushToCar(product) {
    this.arrayProducts.push(product);
    this.arrayCantidades.push("1");
  }

  removeToCar(){
    this.arrayProducts = [];
    this.arrayCantidades = [];
  }

  createFact(sale){
    console.log("ventas"+ JSON.stringify(sale));
    return this.http.post<any>(this.URL + '/doShop',sale);
  }

}

