import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private URL = 'http://localhost:3000/api'
  isAdmin = false;

   user = {
    name: '',
    email: '',
    password: '',
    phone: '',
    address: '',
  }

  constructor(
    private http: HttpClient
  ) { }

  updateEmail(email) {
    this.user.email = email;
    console.log('Correo actualizado, usuario: ' + this.user.email);
  }

  createUserDB(user) {
    return this.http.post<any>(this.URL, user);
  }

  loginUserDB(user) {
    return this.http.post<any>(this.URL + '/login', user);
  }

  getUserByEmailDB() {
    return this.http.post<any>(this.URL + '/getUser', this.user);
  }

  deleteUserByEmailDB() {
    return this.http.post<any>(this.URL + '/deleteUser', this.user);
  }

  updateUserByEmailDB(user) {
    console.log(user)
    return this.http.post<any>(this.URL + '/updateUser', user);
  }

  checkUserDB(user) {
    if (user.name == "" || user.email == "" || user.password == "" || user.address == "" || user.phone == "")
      return false
    else
      return true
  }

  adminLoged(){
    this.isAdmin = true;
  }

  adminLogout(){
    this.isAdmin = false;
  }

  getpasswordUser(){
    console.log(this.user.password)
    return this.user.password
  }
}
