import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ProductService } from '../services/product.service';

@Component({
  selector: 'app-gamer',
  templateUrl: './gamer.component.html',
  styleUrls: ['./gamer.component.css']
})
export class GamerComponent implements OnInit {


  productsGamer = [];
  productsGamerFinal = [];

  getAccessories(){
    this.product_service.getAllProduct().subscribe(
     res=>{
       if(res!=null || res != undefined)
         this.productsGamer = res
         this.productsGamerFinal = res.filter(item=>item.category.includes("1"));
         console.log("desk: " +JSON.stringify(this.productsGamerFinal));
     }
   );
 }

 goToDetails(product){
  this.product_service.setProductDetails(product)
  this.router.navigate(['/detailsProd'])
}
  constructor(
    private product_service : ProductService,
    private router : Router
  ) { }

  ngOnInit(): void {
    this.getAccessories()
  }

}
